    const localization = container.resolve<ILocalization>(ILocalization);\n    const tableMethods = container.resolve<ITableMethods>(ITableMethods);\n    const applicationVersionUseCase = container.resolve<IApplicationVersionUseCase>(IApplicationVersionUseCase);\n    const emit = defineEmits([\"hideLeftNav\", \"showLeftNav\", \"showHeader\", \"hideHeader\", \"close\"]);\n    const appVersions = ref<Array<IAppVersionExtended>>([]);\n    const selectedAppVersion = computed(() => appVersions.value.find(ver => ver.selected));\n    const title = ref(\"App Versions\");\n    const pageDesc = ref(\"You can see versions of your application.\");\n    \n\n    const tableColumns = ref([\"select\", \"version\", \"shortComment\", \"createdBy\", \"createDate\", \"actions\"]);\n    const tableOptions = ref({\n        filterable: true,\n        perPage: 5,\n        perPageValues: [],\n        headings: {\n            select: \"\",\n            version: localization.get(\"version\"),\n            shortComment: localization.get(\"description\"),\n            createdBy: localization.get(\"createdBy\"),\n            createDate: localization.get(\"createDate\"),\n            actions: \"\"\n        },\n        resizableColumns: false,\n        skin: 'app-versions',\n        texts: {\n            count: \"\",\n            noResults: \"No matching records\",\n        },\n    })\n    onMounted(() => {\n        listAppVersions();\n        emit(\"hideHeader\");\n    })\n    defineExpose({listAppVersions})\n    function listAppVersions() {\n        const appID = props.app!.ID;\n        executor.execute(async () => {\n            appVersions.value = await applicationVersionUseCase.listApplicationVersions(appID);\n        }, {loading: true})\n    \n    }\n\n    function selectAppVersion(appVersionId: string) {\n        appVersions.value.forEach(ver => {\n            if(ver.ID == appVersionId) {\n                ver.selected = true;\n            }\n            else {\n                ver.selected = false;\n            }\n        })\n    }\n    function close() {\n        emit(\"close\");\n    }\n    function footerCallback() {\n        if(!selectedAppVersion.value) { return; }\n        props.footerBtns && props.footerBtns.callBack(selectedAppVersion.value.ID);\n        close();\n    }\n    function addButtonCallback() {\n        props.addButton && props.addButton.callBack();\n        close();\n    }\n\n</script>\n<template>\n    <div :style=\"{padding: showTitleAndDesc ? '0' : '20px'}\">\n        <div v-if=\"showTitleAndDesc\" class=\"title\">{{ title }}</div>\n        <span v-if=\"showTitleAndDesc\" class=\"page-desc\">{{ pageDesc }}</span>\n        <div class=\"vue-table\" style=\"margin-top: 32px;\">\n            <button v-if=\"addButton\" class=\"add-new-version\" @click=\"addButtonCallback()\">{{ addButton?.buttonText }}</button>\n            <v-client-table ref=\"table\" class=\"versions-table\" :data= \"appVersions\" :columns=\"tableColumns\" :options=\"tableOptions\">\n                <template v-if=\"selectionType\" #select=\"props\">\n                    <RadioButtonComp :active=\"props.row.selected\" :type=\"'preview'\" @click=\"selectAppVersion(props.row.ID)\"></RadioButtonComp>\n                </template>\n                <template #createDate=\"props\">\n                    <span>{{ tableMethods.formatDate(props.row.createDate) }}</span>\n                </template>\n                <template v-if=\"rowActionButtons\" #actions=\"props\">\n                    <i v-for=\"button in rowActionButtons\" :key=\"button.icon\" :class=\"button.icon + ' action-icon'\" @click=\"button.callBack(props.row.ID)\"></i>\n                </template>\n            </v-client-table>\n        </div>\n        <div v-if=\"footerBtns\" class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button :disabled=\"!selectedAppVersion\" class=\"save-button\" @click=\"footerCallback()\">{{ footerBtns.buttonText }}</button>\n        </div>\n\n    </div>\n</template>\n\n<style scoped>\n    @import url(\"../../../../styles/IDE3.css\");\n    .add-new-version {\n        border-radius: 4px;\n        border: 1px solid #151233;\n        color: #151233;\n        font-weight: 500;\n        font-size: 14px;\n        background-color: transparent;\n        position: absolute;\n        right: 40px;\n        width: 94px;\n        height: 32px;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n    }\n    .action-icon {\n        font-size: 16px;\n        color: #757575;\n        cursor: pointer;\n\n    }\n    .title {\n        font-style: normal;\n        font-weight: 500;\n        font-size: 20px;\n        line-height: 23px;\n        white-space: nowrap;\n        color: #151233;\n        margin-top: 40px;\n        margin-bottom: 8px;\n    }\n    .page-desc {\n        font-size: 16px;\n        color: #616161;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        position: sticky;\n        bottom: 0;\n        background: #FFFFFF;\n        padding: 40px 0 20px;\n    }\n\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n\n    .save-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n</style>\n"],"sourceRoot":""}]);
